// General Overrides
*,
*:after,
*:before{
  box-sizing:border-box;
  padding: 0;
  margin: 0;
}
html,
body{
  float: left;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font-size: 16px;
  background: #fff;
  font-family: 'Lato', sans-serif;
  text-rendering:optimizeLegibility;
}
a:focus{
  outline: 0;
}
img{
  width: 100%;
  height: auto;
  float: left;
}
#admin-menu{
  box-shadow: none;
}

// Input fields
input,
button,
textarea{
  outline: 0;
  border: 0;
  width: 100%;
  padding: 1vw;
  font-size: 1.2vw;
  border-radius: 3px;
  line-height: 1.5;
  -webkit-appearance: none;
  transition: all .2s ease;
  &:focus,
  &:hover{
    transition: all .2s ease;
  }
}
input[type='submit']{
  color: #fff;
  background: $cyan;
  border-radius: 47px;
  text-transform: uppercase;
}
.form-item{
  margin: 0 0 .5rem 0;
  input{
    background: rgba(255,255,255,.85);
    &:focus,
    &:hover{
      background: rgba(255,255,255,.95);
    }
  }
}

// Type formatting
p{
  font-size: 1.2vw;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  color: #333;
  line-height: 2;
  float: left;
  width: 100%;
  margin: 1vw 0;
}
a{
  color: $violet;
  text-decoration: none;
}
ul{
  list-style: none;
  padding: 0;
}
li{
  margin: auto;
  list-style: none;
  float: left;
}
h1{
  font-size: 4vw;
  font-family: 'Lato', sans-serif;
  font-weight: 900;
  color: #333;
  margin: 0;
  padding: .5em 0 0;
}
h2{
  margin: 0;
}
.field-name-body,
.field-name-field-body{
  a{
    color: $violet;
    overflow: hidden;
    font-weight: 500;
    position: relative;
    display: inline-block;
    outline: none;
    vertical-align: bottom;
    text-decoration: none;
    &:before{
      content: '';
      position: absolute;
      bottom: .4rem;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 2px;
      background: $crimson;
      transition: transform 0.2s;
      transform: scaleY(1) translateX(-105%);
    }
    &:hover:before,
    &:focus:before{
      -webkit-transform: scaleY(1) translateX(0);
      transform: scaleY(1) translateX(0);
    }
  }
}

/* 
* structural containers
* -------------------------
*/
#site{
  z-index: 10;
  display: inline-block;
  width: 100%;
  height: 100%;
}

/* 
* messages
* -------------------------
*/
.messages{
  float: left;
  width: 100%;
  padding: 1rem;
  background: $midnight;
  color: $peach;
  a{
    color: $peach;
    text-decoration: underline;
  }
  &.error{
    background: $crimson;
    color: #fff;
    a{
      color: #fff;
      text-decoration: underline;
    }
  }
}
